@import url(https://fonts.googleapis.com/css2?family=Dancing+Script&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Caveat&family=Dancing+Script&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
    width: 100%;
    margin: 50px auto;
}

h1{
    font-family: 'Dancing Script', cursive;
    text-align: center;
    color:#4da6ff;
}

.contact-card{
    padding: 20px;
    margin: 50px auto 20px auto;
    width:85%;
    height: 150px;
    background-color:  #ffd9b3;
    border-radius: 20px;
    box-shadow: 1px 2px 10px #e6e6e6;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    font-family: 'Caveat', cursive;
}
.contact-card div{
    margin: 0 10px;
    
}

.contact-card img {
   
    border-radius: 20px;
}

.contact-card  button{
    padding: 10px;
    background-color:white;
    border-radius: 20px;
    border: none;
    outline-style: none;
    box-shadow: 1px 2px 10px #cce6ff ;
    font-family: 'Dancing Script', cursive;
    font-weight: 600;
}

.contact-card  button:active{
    background-color:#4da6ff ;
}

.contact-card p{
    margin: 10px 0;
    font-size: 0.9em;
}

footer p{
    text-align: center;
}


@media only screen and (min-width:767px){
    .contact-card{
        width:450px;
        height:200px;
    }
    .contact-card p{
        font-size: 1.4em;
    }
    
    .contact-card div{
        margin: 0 30px;
        
    }
    .contact-card img {
        padding: 10px;
        border-radius: 20px;
    }
    

}
